import checkForDomain from './checkForDomain';

export class Investment {
  constructor() {
    this.investment = document.querySelector('.js__investment');
    this.navigation = this.investment.querySelectorAll('[data-investment-nav]');
    this.tabList = Array.from(this.investment.querySelectorAll('[data-investment-tab]'));

    this.init();
  }

  init() {
    if(!checkForDomain()) {
      return;
    }
    this.navigation.forEach((navItem) => {
      navItem.addEventListener('click', this.openTab.bind(this, navItem));
    })
  }

  openTab(button) {
    const dataIndex = button.dataset.investmentNav;
    this.navigation.forEach((navButton) => navButton.classList.remove('is-active'));
    button.classList.add('is-active');

    this.tabList.forEach((tab) => tab.classList.remove('is-active'));
    const activeTab = this.tabList.find((tab) => tab.dataset.investmentTab === dataIndex);
    if(activeTab) {
      activeTab.classList.add('is-active');
    }
  }
}