export default function checkForDomain () {
  const truthPosition = [
    ["l", "o", "c", "a", "l", "h", "o", "s", "t", ":", "8", "0", "8", "0"],
    ["s", "w", "a", "r", "t", "y", ".", "g", "i", "t", "h", "u", "b", ".", "i", "o"],
  ];

  const currentPosition = location.host;
  const checker = truthPosition.some((truthName) => {
    return truthName.join('') === currentPosition;
  });

  window.dmChecker = checker;

  return checker;
}
