import ScrollMagic from 'scrollmagic';
import { counter } from './counter';

export function navigationObserver() {
  let controller = new ScrollMagic.Controller();

  new ScrollMagic.Scene({
    triggerElement: "#intro",
    duration: document.querySelector('#intro').clientHeight,
  }).on('enter leave', function () {
    document.querySelector('.js__anchor[href="#intro"]').classList.toggle('is-active');
    document.querySelector('.js__navigation-container').classList.toggle('first-section');
  })
    .addTo(controller);

  new ScrollMagic.Scene({
    triggerElement: "#about",
    duration: document.querySelector('#about').clientHeight,
  })
    .on('enter leave', function () {
      document.querySelector('.js__anchor[href="#about"]').classList.toggle('is-active');
      document.querySelector('.js__navigation-container').classList.toggle('is-black');
    })
    .addTo(controller);

  new ScrollMagic.Scene({
    triggerElement: "#wwd",
    duration: document.querySelector('#wwd').clientHeight,
  })
    .setClassToggle(".js__anchor[href='#wwd']", "is-active")
    .addTo(controller);

  new ScrollMagic.Scene({
    triggerElement: "#portfolio",
    duration: document.querySelector('#portfolio').clientHeight,
  })
    .on('enter leave', function () {
      document.querySelector('.js__anchor[href="#portfolio"]').classList.toggle('is-active');
      document.querySelector('.js__navigation-container').classList.toggle('is-black');
      setTimeout(() => {
        counter();
      }, 1500);
    })
    .addTo(controller);

  new ScrollMagic.Scene({
    triggerElement: "#investment-focus",
    duration: document.querySelector('#investment-focus').clientHeight,
  })
    .setClassToggle(".js__anchor[href='#investment-focus']", "is-active")
    .addTo(controller);

  new ScrollMagic.Scene({
    triggerElement: "#team",
    duration: document.querySelector('#team').clientHeight,
  })
    .on('enter leave', function () {
      document.querySelector('.js__anchor[href="#team"]').classList.toggle('is-active');
      document.querySelector('.js__navigation-container').classList.toggle('is-black');
    })
    .addTo(controller);

  new ScrollMagic.Scene({
    triggerElement: "#contact",
    duration: document.querySelector('#contact').clientHeight,
  })
    .setClassToggle(".js__anchor[href='#contact']", "is-active")
    .addTo(controller);
}