const header = document.querySelector('.js__header');
const sectionList = document.querySelectorAll('[data-observer]');
let timer = null;
let position = 0;
const DIRECTION_MAP = {
  DOWN: 'down',
  TOP: 'top',
};
let direction = DIRECTION_MAP.DOWN;

function paintHeader(sectionName) {
  if([
    'intro',
    'wwd',
    'investment-focus',
    'contact',
  ].includes(sectionName)) {
    header.classList.remove('is-black');
  } else if ([
    'about',
    'portfolio',
    'team',
  ].includes(sectionName)) {
    header.classList.add('is-black');
  }
}

function headerListener() {
  if(timer !== null) {
    return;
  }

  timer = setTimeout(() => {
    const newPosition = window.pageYOffset;
    if(newPosition > position) {
      direction = DIRECTION_MAP.DOWN;
    } else {
      direction = DIRECTION_MAP.TOP;
    }
    position = newPosition;

    sectionList.forEach((section) => {
      const sectionName = section.dataset.observer;
      const topPosition = section.getBoundingClientRect().top;
      const botPosition = section.getBoundingClientRect().bottom;
      
      if(direction === DIRECTION_MAP.DOWN && topPosition < 100 && topPosition >= 0) {
        paintHeader(sectionName);
      } else if (direction === DIRECTION_MAP.TOP) {
        if(botPosition < 100 && botPosition >= 0) {
          paintHeader(sectionName);
        }
      }
    });

    clearTimeout(timer);
    timer = null;
  }, 5);
}

export function observerFunction () {
  window.addEventListener('scroll', headerListener);
}
