import aos from '../../../node_modules/aos/dist/aos';
import checkForDomain from './checkForDomain';

export function aosAnimation() {
  if(!checkForDomain()) {
    return;
  }

  if(window.matchMedia('(max-width: 1023px)')) {
    document.querySelectorAll('.team__content-block-picture').forEach((picture) => {
      picture.setAttribute('data-aos', 'fade-up');
    });
  }

	aos.init({
		debounceDelay: 50, // the delay on debounce used while resizing window (advanced)
		throttleDelay: 99, // the delay on throttle used while scrolling the page (advanced)
		once: true,
		offset: 0,
		easing: "ease",
		duration: 1000,
	})
}