import { aosAnimation } from './modules/aosAnimation';
import { observerFunction } from './modules/intersectionObserver';
import { navigationObserver } from './modules/navigationObserver';
import { Burger } from './modules/burger';
// import { counter } from './modules/counter';
import { Investment } from './modules/investment';
import { Anchors } from './modules/anchors/anchors';

function onDomLoad(): void {
  aosAnimation();
  new Burger();
  // counter();
  new Investment();
  new Anchors();

  if(!window.dmChecker) {
    document.querySelector('body').innerHTML = '';
  }
  setTimeout(() => {
    navigationObserver();
    observerFunction();
  }, 0);
}

document.addEventListener('DOMContentLoaded', onDomLoad);
